var graceChecker = require('@/common/graceChecker.js');
import pwInput from '@/components/input/PwInput'
export default {
	components: {
		pwInput
	},
	data() {
		return {
			phone: '',
			code: '',
			pay_paw: '',
			pay_pawa: '',
			second: 90,
			showText: true
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
	},
	onLoad() {
		var user = uni.getStorageSync('uerInfo');
		this.phone = user.phone;
	},
	methods: {
		sendCode() {
			//发送验证码
			var _this = this;
			if (_this.phone == '') {
				_this.$tools.toast('请填写正确的手机号码')
				return false;
			}

			//发送验证码
			_this.$request.common
				.sendCode({
					phone: _this.phone,
					type: 1
				})
				.then(data => {
					_this.$tools.loadingHide();
					if (data.status == 200) {
						_this.showText = false;
						var interval = setInterval(() => {
							let times = --_this.second;
							_this.second = times < 10 ? '0' + times : times; //小于10秒补 0
						}, 1000);
						setTimeout(() => {
							clearInterval(interval);
							_this.second = 90;
							_this.showText = true;
						}, 90000);
					} else {
						_this.$tools.toast(data.msg)
					}
				})
				.catch(err => {
					_this.$tools.loadingHide();
					//消息异常
					_this.$tools.toast('数据加载异常')
				});
		},
		btnClick() {
			var _this = this;
			//定义表单规则
			var rule = [{
					name: 'code',
					checkType: 'string',
					checkRule: '6,6',
					errorMsg: '验证码6个字符'
				},
				{
					name: 'pay_paw',
					checkType: 'string',
					checkRule: '6,20',
					errorMsg: '密码为6-20个字符'
				}
			];
			if(this.$refs['pay_paw'].getPassWord() != this.$refs['pay_pawa'].getPassWord()){
				this.$tools.toast('两次密码不相同!');
				return 
			}
			//进行表单检查
			var formData = {
				phone : this.phone,
				code: this.code,
				pay_paw: this.$refs['pay_paw'].getPassWord()
			}

			var checkRes = graceChecker.check(formData, rule);
			if (checkRes) {
				_this.$tools.loading('数据提交中')
				_this.$request.user
					.updPayPsw(formData)
					.then(data => {
						_this.$tools.loadingHide();
						if (data.status == 1) {

							_this.$tools.toast('修改成功')
							setTimeout(()=>{
								uni.navigateTo({
									url: '/pages/member/details'
								});
							},500)

						} else {
							_this.$tools.toast(data.msg)
						}
					})
					.catch(err => {
						_this.$tools.loadingHide();
						//消息异常
						_this.$tools.toast('数据加载异常')
					});
			} else {
				_this.$tools.toast(graceChecker.error)
			}
		}
	}
};
